import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
//import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Shopping } from './shopping';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class ShoppingService {
    productList: Shopping[] = [];
  api = '/shopping';

  constructor(private http: HttpClient) {
  }

    productsByType(productType: string): Observable<Shopping[]> {
        let params = new HttpParams();
        let url = '';
          url = `${this.api}/protucts/type/${productType}`;
          return this.http.get<Shopping[]>(url, {params, headers});
      
      }

      save(entity: Shopping): Observable<Shopping> {        
        var params = new HttpParams();
        var url = "";              
        const headers = new HttpHeaders().set('content-type', 'application/json');          
        url = `${this.api}/order`;
        return this.http.post<Shopping>(url, entity, {headers, params});
             
    }

}